
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {

components: { XInput, Group },
data() {
return {
keyword: "",
searchState: false,
searchContentList: [],
hotList: ["1", "12", "123", "1234", "12345", "1234", "123", "12", "1"]
};
},

methods: {

searchClick() {
if (this.keyword.length > 0) {
this.$store.dispatch("searchStore/saveSearchContent", this.keyword);
this.searchContentList = this.$store.state.searchStore.searchList;

console.log(this.keyword);
} else {
console.log("请输入搜索内容");
}
},

clearClick() {
this.$store.dispatch("searchStore/clearSearchContent", []);
this.searchContentList = this.$store.state.searchStore.searchList;
},

showHotClick() {}
},
created() {},
mounted() {}
};



